import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import 'rxjs/add/operator/map';
@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
  checkme:any;
  constructor(private http: HttpClient) { }
  getEemployees(){
    return this.http.get('http://localhost/api/select.php', ).map((res)=>{
    this.checkme = res;
    if(this.checkme._body !== '0'){
      return res;
    }
  });
  
}
addEmployee(info){
  return this.http.post('http://localhost/api/insert.php', info).map(()=>'');
}

deleteEmployee(id){
  return this.http.post('http://localhost/api/delete.php', {'id': id}).map(()=> this.getEemployees());
}
getEmployee(id){
  return this.http.post('http://localhost/api/selectone.php', {'id': id}).map((res) => res);
}
updateEmployee(info){
  return this.http.post("http://localhost/api/update.php/", info)
  .map(()=>"");
}
}
